@import "https://fonts.googleapis.com/css2?family=Cute+Font&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.calculator {
  max-width: 350px;
  border-radius: 20px;
  margin: 50px auto;
  padding: 25px 20px 30px;
  box-shadow: 0 50px 100px -20px #32325d40, 0 30px 60px -30px #0000004d, inset 0 -4px 8px 2px #0a254059;
}

.display {
  color: #313131;
  text-align: right;
  background-color: #cbcaca;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 5px 10px;
  font-family: Cute Font, sans-serif;
  font-size: 40px;
  box-shadow: inset 3px 3px 6px #464646, inset -3px -3px 6px 1px #8f8f8f80;
}

.buttons {
  grid-gap: 10px;
  grid-template-columns: repeat(4, 1fr);
  display: grid;
}

button {
  color: #333;
  text-align: center;
  cursor: pointer;
  background-color: #eee;
  border: none;
  border-radius: 25px;
  padding: 10px;
  font-size: 24px;
  text-decoration: none;
  transition: all .1s ease-in-out;
  position: relative;
  top: 0;
  box-shadow: 0 .15em #3a3e45, 0 50px 100px -20px #32325daf, 0 30px 60px -30px #0000004d, inset 0 -2px 6px #0a25406d;
}

button:active {
  top: .15em;
  box-shadow: 0 0 #3a3e45, 0 50px 100px -20px #32325d40, 0 30px 60px -30px #0000004d, inset 0 -2px 6px #0a254059;
}

.operator, .clear, .equals {
  color: #fff;
}

.operator {
  background-color: #eaa43a;
}

.clear {
  background-color: #df5f55;
}

.equals {
  background-color: #64a066;
}

